export const supabase = createClient(supabaseUrl, supabaseKey)

// Database types
export interface Course {
  id?: number
  title: string
  description: string
  provider: string
  type: 'FREE' | 'PAID'
  created_at?: string
}

export interface Admin {
  id?: number
  email: string
  password: string
  created_at?: string
}

// Admin functions
export const authenticateAdmin = async (email: string, password: string): Promise<boolean> => {
  try {
    const { data, error } = await supabase
      .from('admins')
      .select('*')
      .eq('email', email)
      .eq('password', password)
      .single()

    if (error) {
      console.error('Auth error:', error)
      return false
    }

    return !!data
  } catch (error) {
    console.error('Authentication failed:', error)
    return false
  }
}

export const createAdmin = async (email: string, password: string): Promise<boolean> => {
  try {
    const { error } = await supabase
      .from('admins')
      .insert([{ email, password }])

    if (error) {
      console.error('Create admin error:', error)
      return false
    }

    return true
  } catch (error) {
    console.error('Failed to create admin:', error)
    return false
  }
}

// Course functions
export const getCourses = async (): Promise<Course[]> => {
  try {
    const { data, error } = await supabase
      .from('courses')
      .select('*')
      .order('created_at', { ascending: false })

    if (error) {
      console.error('Get courses error:', error)
      return []
    }

    return data || []
  } catch (error) {
    console.error('Failed to get courses:', error)
    return []
  }
}

export const addCourse = async (course: Omit<Course, 'id' | 'created_at'>): Promise<boolean> => {
  try {
    const { error } = await supabase
      .from('courses')
      .insert([course])

    if (error) {
      console.error('Add course error:', error)
      return false
    }

    return true
  } catch (error) {
    console.error('Failed to add course:', error)
    return false
  }
}

export const deleteCourse = async (id: number): Promise<boolean> => {
  try {
    const { error } = await supabase
      .from('courses')
      .delete()
      .eq('id', id)

    if (error) {
      console.error('Delete course error:', error)
      return false
    }

    return true
  } catch (error) {
    console.error('Failed to delete course:', error)
    return false
  }
}
