"use client"

import { useState } from "react"
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"
import { Plus, Trash2, Eye, EyeOff, UserPlus } from "lucide-react"
import { toast } from "sonner"
import { authenticateAdmin, createAdmin, addCourse, deleteCourse, Course } from "@/lib/supabase"

interface AdminSectionProps {
  isOpen: boolean
  onClose: () => void
  courses: Course[]
  onCoursesUpdate: (courses: Course[]) => void
}

export default function AdminSection({ isOpen, onClose, courses, onCoursesUpdate }: AdminSectionProps) {
  const [isAuthenticated, setIsAuthenticated] = useState(false)
  const [email, setEmail] = useState("tailorshreyash01@gmail.com")
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [showAddForm, setShowAddForm] = useState(false)
  const [showCreateAdmin, setShowCreateAdmin] = useState(false)
  const [deleteId, setDeleteId] = useState<number | null>(null)
  const [loading, setLoading] = useState(false)
  
  // New admin form state
  const [newAdminData, setNewAdminData] = useState({
    email: "",
    password: ""
  })

  // Course form state
  const [formData, setFormData] = useState({
    title: "",
    type: "FREE" as "FREE" | "PAID",
    provider: "",
    description: ""
  })

  const handleAuth = async () => {
    if (!email || !password) {
      toast.error("Please fill in all fields")
      return
    }

    setLoading(true)
    try {
      const isValid = await authenticateAdmin(email, password)
      if (isValid) {
        setIsAuthenticated(true)
        setEmail("tailorshreyash01@gmail.com")
        setPassword("")
        toast.success("Welcome to admin panel!")
      } else {
        toast.error("Invalid credentials!")
        setPassword("")
      }
    } catch (error) {
      toast.error("Authentication failed!")
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  const handleCreateAdmin = async () => {
    if (!newAdminData.email || !newAdminData.password) {
      toast.error("Please fill in all fields")
      return
    }

    setLoading(true)
    try {
      const success = await createAdmin(newAdminData.email, newAdminData.password)
      if (success) {
        toast.success("Admin account created successfully!")
        setNewAdminData({ email: "", password: "" })
        setShowCreateAdmin(false)
      } else {
        toast.error("Failed to create admin account")
      }
    } catch (error) {
      toast.error("Failed to create admin account")
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  const handleClose = () => {
    setIsAuthenticated(false)
    setEmail("tailorshreyash01@gmail.com")
    setPassword("")
    setShowAddForm(false)
    setShowCreateAdmin(false)
    setNewAdminData({ email: "", password: "" })
    setFormData({
      title: "",
      type: "FREE",
      provider: "",
      description: ""
    })
    onClose()
  }

  const handleAddCourse = async () => {
    if (!formData.title || !formData.provider || !formData.description) {
      toast.error("Please fill in all required fields")
      return
    }

    setLoading(true)
    try {
      const success = await addCourse(formData)
      if (success) {
        // Refresh courses list by calling parent component
        window.location.reload() // Simple refresh for now
        setFormData({
          title: "",
          type: "FREE",
          provider: "",
          description: ""
        })
        setShowAddForm(false)
        toast.success("Course added successfully!")
      } else {
        toast.error("Failed to add course")
      }
    } catch (error) {
      toast.error("Failed to add course")
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  const handleDeleteCourse = async (id: number) => {
    setLoading(true)
    try {
      const success = await deleteCourse(id)
      if (success) {
        // Refresh courses list
        window.location.reload() // Simple refresh for now
        setDeleteId(null)
        toast.success("Course deleted successfully!")
      } else {
        toast.error("Failed to delete course")
      }
    } catch (error) {
      toast.error("Failed to delete course")
      console.error(error)
    } finally {
      setLoading(false)
    }
  }

  if (!isOpen) return null

  return (
    <Dialog open={isOpen} onOpenChange={handleClose}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>Admin Panel</DialogTitle>
          <DialogDescription>
            Manage courses for Certiswift
          </DialogDescription>
        </DialogHeader>

        {!isAuthenticated ? (
          <div className="space-y-4">
            <div className="text-center">
              <h3 className="text-lg font-semibold mb-4">Admin Login</h3>
              <div className="max-w-sm mx-auto space-y-4">
                <div className="space-y-2">
                  <Input
                    type="email"
                    placeholder="Admin Email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="bg-background"
                  />
                  <div className="relative">
                    <Input
                      type={showPassword ? "text" : "password"}
                      placeholder="Password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                      onKeyPress={(e) => e.key === "Enter" && handleAuth()}
                      className="bg-background"
                    />
                    <Button
                      type="button"
                      variant="ghost"
                      size="sm"
                      className="absolute right-0 top-0 h-full px-3 py-2 hover:bg-transparent"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? (
                        <EyeOff className="h-4 w-4" />
                      ) : (
                        <Eye className="h-4 w-4" />
                      )}
                    </Button>
                  </div>
                </div>
                <div className="space-y-2">
                  <Button onClick={handleAuth} className="w-full" disabled={loading}>
                    {loading ? "Authenticating..." : "Login"}
                  </Button>
                  <Button 
                    variant="outline" 
                    onClick={() => setShowCreateAdmin(!showCreateAdmin)} 
                    className="w-full"
                  >
                    <UserPlus className="w-4 h-4 mr-2" />
                    Create New Admin Account
                  </Button>
                </div>
              </div>

              {/* Create Admin Form */}
              {showCreateAdmin && (
                <Card className="mt-6 max-w-md mx-auto">
                  <CardHeader>
                    <CardTitle>Create Admin Account</CardTitle>
                    <CardDescription>Add a new administrator to the system</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <Input
                      type="email"
                      placeholder="New Admin Email"
                      value={newAdminData.email}
                      onChange={(e) => setNewAdminData({...newAdminData, email: e.target.value})}
                      className="bg-background"
                    />
                    <Input
                      type="password"
                      placeholder="New Admin Password"
                      value={newAdminData.password}
                      onChange={(e) => setNewAdminData({...newAdminData, password: e.target.value})}
                      className="bg-background"
                    />
                    <div className="flex gap-2">
                      <Button onClick={handleCreateAdmin} disabled={loading} className="flex-1">
                        {loading ? "Creating..." : "Create Admin"}
                      </Button>
                      <Button variant="outline" onClick={() => setShowCreateAdmin(false)} className="flex-1">
                        Cancel
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              )}
            </div>
          </div>
        ) : (
          <div className="space-y-6">
            {/* Add Course Section */}
            <div className="border-b pb-4">
              <div className="flex items-center justify-between mb-4">
                <h3 className="text-lg font-semibold">Course Management</h3>
                <Button 
                  onClick={() => setShowAddForm(!showAddForm)}
                  className="flex items-center gap-2"
                >
                  <Plus className="w-4 h-4" />
                  Add Course
                </Button>
              </div>

              {showAddForm && (
                <Card>
                  <CardHeader>
                    <CardTitle>Add New Course</CardTitle>
                    <CardDescription>Fill in the course details below</CardDescription>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div className="space-y-2">
                        <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">Course Title *</label>
                        <Input
                          placeholder="e.g., Advanced React Patterns"
                          value={formData.title}
                          onChange={(e) => setFormData({...formData, title: e.target.value})}
                          className="bg-background"
                        />
                      </div>
                      <div className="space-y-2">
                        <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">Provider *</label>
                        <Input
                          placeholder="e.g., Meta, Google, Microsoft"
                          value={formData.provider}
                          onChange={(e) => setFormData({...formData, provider: e.target.value})}
                          className="bg-background"
                        />
                      </div>
                    </div>
                    
                    <div className="space-y-2">
                      <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">Type *</label>
                      <Select value={formData.type} onValueChange={(value: "FREE" | "PAID") => setFormData({...formData, type: value})}>
                        <SelectTrigger className="bg-background">
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="FREE">Free</SelectItem>
                          <SelectItem value="PAID">Paid</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    <div className="space-y-2">
                      <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">Description *</label>
                      <Textarea
                        placeholder="Describe what students will learn..."
                        value={formData.description}
                        onChange={(e) => setFormData({...formData, description: e.target.value})}
                        rows={3}
                        className="bg-background"
                      />
                    </div>

                    <div className="flex gap-2 pt-2">
                      <Button onClick={handleAddCourse} disabled={loading}>
                        {loading ? "Adding..." : "Add Course"}
                      </Button>
                      <Button variant="outline" onClick={() => setShowAddForm(false)}>Cancel</Button>
                    </div>
                  </CardContent>
                </Card>
              )}
            </div>

            {/* Existing Courses */}
            <div>
              <h3 className="text-lg font-semibold mb-4">Existing Courses ({courses.length})</h3>
              {courses.length === 0 ? (
                <p className="text-muted-foreground text-center py-8">No courses available. Add your first course above!</p>
              ) : (
                <div className="space-y-3 max-h-96 overflow-y-auto">
                  {courses.map((course) => (
                    <Card key={course.id} className="bg-card">
                      <CardContent className="pt-4">
                        <div className="flex items-start justify-between">
                          <div className="flex-1 min-w-0">
                            <div className="flex items-center gap-2 mb-2">
                              <h4 className="font-semibold truncate">{course.title}</h4>
                              <div className={`inline-flex items-center rounded-md px-2 py-1 text-xs font-medium ring-1 ring-inset ${
                                course.type === "FREE" 
                                  ? "bg-green-50 text-green-700 ring-green-600/20" 
                                  : "bg-primary/10 text-primary ring-primary/20"
                              }`}>
                                {course.type}
                              </div>
                            </div>
                            <p className="text-sm text-primary font-medium mb-1">{course.provider}</p>
                            <p className="text-sm text-muted-foreground line-clamp-2">{course.description}</p>
                          </div>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => course.id && setDeleteId(course.id)}
                            className="ml-4 flex-shrink-0"
                            disabled={loading}
                          >
                            <Trash2 className="w-4 h-4" />
                          </Button>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </DialogContent>

      {/* Delete Confirmation Dialog */}
      <AlertDialog open={!!deleteId} onOpenChange={() => setDeleteId(null)}>
        <AlertDialogContent>
          <AlertDialogHeader>
            <AlertDialogTitle>Delete Course</AlertDialogTitle>
            <AlertDialogDescription>
              Are you sure you want to delete this course? This action cannot be undone.
            </AlertDialogDescription>
          </AlertDialogHeader>
          <AlertDialogFooter>
            <AlertDialogCancel>Cancel</AlertDialogCancel>
            <AlertDialogAction
              onClick={() => deleteId && handleDeleteCourse(deleteId)}
              className="bg-red-600 hover:bg-red-700"
              disabled={loading}
            >
              {loading ? "Deleting..." : "Delete"}
            </AlertDialogAction>
          </AlertDialogFooter>
        </AlertDialogContent>
      </AlertDialog>
    </Dialog>
  )
}
